/* 锚样式 - 像素科幻风格，冷色调 */

/* ========== CSS 变量定义 ========== */
:root {
  /* 冷酷极客色调 - 更暗、更冷、更科技 */
  --primary-cyan: hsl(180, 100%, 40%);
  --primary-blue: hsl(200, 100%, 35%);
  --primary-indigo: hsl(240, 80%, 40%);
  --accent-teal: hsl(170, 100%, 35%);
  --accent-purple: hsl(270, 80%, 45%);
  --accent-green: hsl(120, 60%, 40%);
  --accent-red: hsl(0, 70%, 45%);
  --accent-orange: hsl(30, 80%, 45%);

  /* 极暗背景 - 更冷酷 */
  --bg-primary: hsl(220, 15%, 8%);
  --bg-secondary: hsl(220, 12%, 12%);
  --bg-tertiary: hsl(220, 15%, 15%);
  --bg-card: hsl(220, 18%, 10%);
  --bg-glass: hsla(220, 20%, 12%, 0.8);

  /* 文字颜色 - 更冷 */
  --text-primary: hsl(180, 20%, 88%);
  --text-secondary: hsl(180, 15%, 65%);
  --text-tertiary: hsl(180, 10%, 50%);

  /* 边框和阴影 - 更暗、更锐利 */
  --border-color: hsl(180, 30%, 20%);
  --border-glow: hsl(180, 100%, 40%);
  --shadow-sm: 0 1px 4px hsla(0, 0%, 0%, 0.5);
  --shadow-md: 0 2px 8px hsla(0, 0%, 0%, 0.6);
  --shadow-lg: 0 4px 16px hsla(0, 0%, 0%, 0.7);
  --shadow-glow: 0 0 12px hsla(180, 100%, 40%, 0.3);

  /* 渐变 */
  --gradient-primary: linear-gradient(135deg, var(--primary-cyan), var(--primary-blue));
  --gradient-card: linear-gradient(135deg, var(--bg-card), var(--bg-tertiary));
  --gradient-glass: linear-gradient(135deg, hsla(220, 30%, 25%, 0.8), hsla(220, 30%, 20%, 0.6));

  /* 现代 HUD 面板 */
  --radius-xs: 6px;
  --radius-sm: 10px;
  --radius-md: 14px;
  --radius-lg: 18px;

  --panel-border: hsla(187, 85%, 45%, 0.22);
  --panel-border-strong: hsla(187, 85%, 55%, 0.42);
  --panel-glow: 0 0 0 1px var(--panel-border) inset, 0 0 20px hsla(187, 85%, 45%, 0.12);
  --panel-bg: linear-gradient(180deg, hsla(220, 18%, 12%, 0.96), hsla(220, 22%, 8%, 0.96));
  --panel-bg-hover: linear-gradient(180deg, hsla(220, 18%, 14%, 0.98), hsla(220, 22%, 9%, 0.98));
  --panel-border-gradient: linear-gradient(
    135deg,
    hsla(180, 100%, 45%, 0.55),
    hsla(200, 100%, 40%, 0.18) 35%,
    hsla(270, 80%, 45%, 0.12) 70%,
    transparent
  );

  /* 动画时间 */
  --transition-fast: 0.15s;
  --transition-normal: 0.3s;
  --transition-slow: 0.5s;
}

/* ========== 全局样式 ========== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family:
    'Inter',
    'Roboto',
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    sans-serif;
  background: var(--bg-primary);
  color: var(--text-primary);
  font-size: 1rem; /* 调大字体 */
  line-height: 1.8; /* 增加行间距 */
  overflow: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* 像素风格字体（可选） */
@font-face {
  font-family: 'Pixel';
  src: url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
}

/* ========== 主应用容器 ========== */
.space-station-app {
  width: 100%;
  min-height: 100%;
  height: 100%;
  position: relative;
  color: var(--text-primary);
  overflow: hidden;
  background: var(--bg-primary);
}

/* 全屏模式支持 */
html:fullscreen .space-station-app,
html:-webkit-full-screen .space-station-app,
html:-moz-full-screen .space-station-app,
html:-ms-fullscreen .space-station-app {
  width: 100vw !important;
  height: 100vh !important;
}

/* ========== 屏幕容器 ========== */
.screen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: none;
  opacity: 0;
  transition: opacity var(--transition-slow);
  overflow: hidden;
}

.screen.active {
  display: flex;
  opacity: 1;
  z-index: 10;
}

/* ========== 开始屏幕 ========== */
.start-screen {
  align-items: center;
  justify-content: center;
  background: radial-gradient(circle at 50% 50%, hsla(180, 100%, 40%, 0.15) 0%, transparent 70%), var(--bg-primary);
  flex-direction: column;
}

.start-screen-content {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  animation: fadeInUp 1s ease-out;
}

.start-screen-logo {
  font-size: 4rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 20px var(--primary-cyan));
  margin-bottom: 1rem;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 20px var(--primary-cyan));
  }
  50% {
    transform: scale(1.05);
    filter: drop-shadow(0 0 30px var(--primary-cyan));
  }
}

.start-screen-title {
  font-size: 2rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  margin: 0;
}

.start-screen-subtitle {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin: 0;
}

.start-screen-button {
  margin-top: 2rem;
  padding: 0.75rem 2rem;
  background: var(--primary-cyan);
  border: 2px solid var(--primary-cyan);
  border-radius: 4px;
  color: var(--bg-primary);
  font-size: 0.9rem;
  font-weight: 600;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  transition: all var(--transition-normal);
  box-shadow: 0 0 20px hsla(180, 100%, 40%, 0.3);
}

.start-screen-button:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  transform: translateY(-2px);
  box-shadow: 0 0 30px hsla(170, 100%, 35%, 0.5);
}

.start-screen-button:active {
  transform: translateY(0);
}

.start-screen-hint {
  margin-top: 1rem;
  font-size: 0.7rem;
  color: var(--text-tertiary);
  font-family: 'Courier New', monospace;
  animation: blink 2s ease-in-out infinite;
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.3;
  }
}

/* ========== 标题页面 ========== */
.splash-screen {
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at 30% 30%, hsla(180, 100%, 40%, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, hsla(200, 100%, 35%, 0.1) 0%, transparent 50%), var(--bg-primary);
  flex-direction: column;
}

.splash-screen-content {
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  animation: fadeInUp 0.8s ease-out;
}

.splash-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.splash-logo {
  font-size: 3rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 15px var(--primary-cyan));
}

.splash-title {
  font-size: 1.75rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  margin: 0;
}

.splash-subtitle {
  font-size: 0.7rem;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.3em;
  margin: 0;
}

.splash-actions {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  width: 100%;
  max-width: 300px;
}

.splash-button {
  padding: 0.75rem 1.5rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.85rem;
  font-weight: 600;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  transition: all var(--transition-fast);
}

.splash-button.primary {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.splash-button.primary:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  box-shadow: 0 0 20px hsla(170, 100%, 35%, 0.4);
}

.splash-button.secondary:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
}

.splash-button.icon-only {
  width: 40px;
  height: 40px;
  padding: 0;
  border-radius: 50%;
  margin: 0 auto;
}

.splash-button.icon-only:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  transform: scale(1.1);
}

.splash-footer {
  margin-top: auto;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.splash-copyright,
.splash-author {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-family: 'Courier New', monospace;
  margin: 0;
}

/* ========== 主游戏界面 ========== */
.main-screen {
  flex-direction: column;
  background:
    radial-gradient(circle at 20% 50%, hsla(187, 85%, 45%, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 80% 80%, hsla(240, 60%, 50%, 0.1) 0%, transparent 50%), var(--bg-primary);
}

.space-station-app::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* 更现代：淡网格 + 很轻微的舱壁扫描线（避免“像素小游戏”感） */
  background-image:
    linear-gradient(90deg, hsla(187, 85%, 45%, 0.035) 1px, transparent 1px),
    linear-gradient(0deg, hsla(187, 85%, 45%, 0.03) 1px, transparent 1px),
    repeating-linear-gradient(0deg, transparent 0 10px, hsla(0, 0%, 100%, 0.012) 10px 11px);
  background-size:
    72px 72px,
    72px 72px,
    100% 100%;
  background-position: center, center, center;
  pointer-events: none;
  z-index: 0;
}

/* ========== 统一的“舰体 HUD 面板”卡片质感 ========== */
:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
) {
  position: relative;
  isolation: isolate;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-sm);
  box-shadow: var(--shadow-md), var(--panel-glow);
}

:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
)::before {
  z-index: 2;
}

:where(
  .status-card,
  .entry-item,
  .monitor-item,
  .detail-section,
  .maintext-container,
  .message-column,
  .chat-options-container,
  .chat-no-options-content,
  .setup-screen-content,
  .module-container
)::after {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: inherit;
  pointer-events: none;
  z-index: 1;
  /* 内高光（去掉斜向条纹，避免花眼） */
  background: linear-gradient(180deg, hsla(0, 0%, 100%, 0.06), transparent 28%, transparent 70%, hsla(0, 0%, 0%, 0.25));
  opacity: 0.9;
  mix-blend-mode: normal;
}

:where(
    .status-card,
    .entry-item,
    .monitor-item,
    .detail-section,
    .maintext-container,
    .message-column,
    .chat-options-container,
    .chat-no-options-content,
    .setup-screen-content,
    .module-container
  )
  > * {
  position: relative;
  z-index: 3;
}

/* ========== 开局设置页面 ========== */
.setup-screen {
  align-items: center;
  justify-content: center;
  background:
    radial-gradient(circle at 30% 30%, hsla(180, 100%, 40%, 0.1) 0%, transparent 50%),
    radial-gradient(circle at 70% 70%, hsla(200, 100%, 35%, 0.1) 0%, transparent 50%), var(--bg-primary);
  flex-direction: column;
}

.setup-screen-content {
  width: 100%;
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
  background:
    var(--gradient-glass) padding-box,
    var(--panel-border-gradient) border-box;
  border-radius: var(--radius-md);
  animation: fadeInUp 0.8s ease-out;
}

.setup-header {
  text-align: center;
}

.setup-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--primary-cyan);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin: 0 0 0.5rem 0;
}

.setup-subtitle {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin: 0;
}

.setup-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.setup-form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.setup-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  font-weight: 600;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.setup-input,
.setup-select {
  padding: 0.5rem 0.75rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.8rem;
  font-family: 'Courier New', monospace;
  transition: all var(--transition-fast);
}

.setup-input:focus,
.setup-select:focus {
  outline: none;
  box-shadow:
    0 0 0 2px hsla(187, 85%, 45%, 0.22),
    0 0 20px hsla(187, 85%, 45%, 0.12);
}

.setup-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  margin-top: 0.5rem;
}

.setup-button {
  padding: 0.6rem 1.25rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.8rem;
  font-weight: 600;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all var(--transition-fast);
}

.setup-button.primary {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.setup-button.primary:hover {
  background: var(--accent-teal);
  border-color: var(--accent-teal);
  box-shadow: 0 0 20px hsla(170, 100%, 35%, 0.4);
}

.setup-button.secondary:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
}

/* ========== 主游戏界面内容 ========== */
.main-screen {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

/* ========== 顶部导航栏 ========== */
.main-header {
  background: var(--bg-glass);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-bottom: 1px solid var(--border-color);
  box-shadow: var(--shadow-sm);
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 0.5rem 0.75rem;
  flex-shrink: 0;
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  max-width: 1400px;
  margin: 0 auto;
}

.logo-section {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.logo-icon {
  font-size: 1.25rem;
  color: var(--primary-cyan);
  filter: drop-shadow(0 0 6px var(--primary-cyan));
}

.logo-text {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--primary-cyan);
  letter-spacing: 0.1em;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
}

.header-actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.header-status {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* ========== 全屏按钮样式 ========== */
.fullscreen-button {
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  padding: 0.4rem 0.6rem;
  color: var(--text-primary);
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.9rem;
  min-width: 36px;
  height: 36px;
}

.fullscreen-button:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
  color: var(--primary-cyan);
  transform: scale(1.05);
}

.fullscreen-button:active {
  transform: scale(0.95);
}

.start-screen .fullscreen-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 100;
}

.status-indicator {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  padding: 0.3rem 0.6rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border-radius: var(--radius-xs);
  border: 1px solid transparent;
  font-size: 0.75rem;
}

.status-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--accent-green);
  box-shadow: 0 0 3px var(--accent-green);
}

.status-indicator.online .status-dot {
  background: var(--accent-teal);
  box-shadow: 0 0 10px var(--accent-teal);
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.1);
  }
}

.status-text {
  font-size: 0.7rem;
  color: var(--text-secondary);
  font-weight: 500;
  font-family: 'Courier New', monospace;
}

/* ========== 主内容区域 ========== */
.main-content {
  display: flex;
  flex-direction: column;
  flex: 1;
  max-width: 100%;
  margin: 0 auto;
  width: 100%;
  padding: 0;
  gap: 0;
  position: relative;
  z-index: 1;
  overflow: hidden;
  min-height: 0;
}

/* ========== 顶部模块切换导航 ========== */
.top-nav-menu {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex: 1;
  justify-content: center;
}

.nav-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.6rem 1rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.9rem; /* 调大字体 */
  line-height: 1.6; /* 增加行间距 */
  cursor: pointer;
  transition: all var(--transition-normal) ease;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  overflow: hidden;
  text-align: left;
  width: 100%;
  font-family: 'Courier New', monospace;
}

.nav-item::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform var(--transition-normal);
}

.nav-item:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
  color: var(--text-primary);
}

.nav-item:hover::before {
  transform: scaleX(1);
}

.nav-item.active {
  background:
    var(--gradient-glass) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  color: var(--text-primary);
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 24px hsla(187, 85%, 45%, 0.16);
}

.nav-item.active::before {
  transform: scaleX(1);
}

.nav-shortcut {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  background: hsla(220, 18%, 12%, 0.85);
  border: 1px solid hsla(187, 85%, 45%, 0.18);
  border-radius: 7px;
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-tertiary);
  margin-right: 0.25rem;
  transition: all var(--transition-fast);
}

.nav-item:hover .nav-shortcut,
.nav-item.active .nav-shortcut {
  background: var(--primary-cyan);
  border-color: var(--primary-cyan);
  color: var(--bg-primary);
}

.nav-icon {
  font-size: 1rem; /* 调大图标 */
  color: var(--primary-cyan);
  transition: transform var(--transition-fast);
  width: 16px;
  text-align: center;
}

.nav-item:hover .nav-icon,
.nav-item.active .nav-icon {
  color: var(--accent-teal);
}

.nav-text {
  flex: 1;
}

/* ========== 主内容区域 ========== */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* ========== 内容面板 ========== */
.content-panel {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding: 0.75rem; /* 添加内边距 */
}

.module-container {
  display: none;
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  background:
    var(--gradient-glass) padding-box,
    var(--panel-border-gradient) border-box;
  border-radius: var(--radius-lg);
  box-shadow:
    var(--shadow-lg),
    0 0 0 1px hsla(187, 85%, 45%, 0.18) inset;
  overflow: hidden;
  flex: 1;
  flex-direction: column;
  min-height: 0;
}

.module-container.active {
  display: flex;
  animation: fadeInUp 0.4s ease-out;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.module-header {
  padding: 0.5rem 0.75rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-glass);
  flex-shrink: 0;
}

.module-title {
  font-size: 0.85rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-primary);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.module-title i {
  color: var(--primary-cyan);
}

/* ========== 主文本框样式 ========== */
.maintext-container {
  margin-bottom: 1.2rem;
  padding: 1rem;
}

.maintext-header {
  display: flex;
  align-items: center;
  margin-bottom: 0.8rem;
  padding-bottom: 0.6rem;
  border-bottom: 1px solid var(--border-color);
}

.maintext-title {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin: 0;
}

.maintext-title i {
  color: var(--primary-cyan);
}

/* 三个区域的容器 */
.maintext-areas {
  display: flex;
  gap: 0.75rem;
  align-items: stretch;
}

/* 每个区域的容器 */
.maintext-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0; /* 允许 flex 子元素收缩 */
}

.maintext-section-header {
  margin-bottom: 0.5rem;
  padding-bottom: 0.4rem;
  border-bottom: 1px solid var(--border-color);
}

.maintext-section-title {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.maintext-section-area {
  flex: 1;
  min-height: 120px;
  max-height: 300px;
  overflow-y: auto;
  padding: 0.8rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.85rem; /* 调大字体 */
  line-height: 1.8; /* 增加行间距 */
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', monospace;
}

.maintext-section-area::-webkit-scrollbar {
  width: 6px;
}

.maintext-section-area::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.maintext-section-area::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.maintext-section-area::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

/* 兼容旧格式：如果只有一个 maintext-area */
.maintext-area {
  min-height: 120px;
  max-height: 300px;
  overflow-y: auto;
  padding: 0.8rem;
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.85rem;
  line-height: 1.6;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', 'Consolas', 'Monaco', monospace;
}

.maintext-area::-webkit-scrollbar {
  width: 6px;
}

.maintext-area::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.maintext-area::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.maintext-area::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

.module-body {
  padding: 0.75rem;
  flex: 1;
  overflow-y: auto;
  min-height: 0;
}

/* ========== 状态卡片 ========== */
.status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}

.status-card {
  padding: 0.6rem 0.75rem;
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.status-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transition: transform var(--transition-normal);
}

.status-card:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-lg),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 28px hsla(187, 85%, 45%, 0.18);
}

.status-card:hover::before {
  transform: scaleX(1);
}

.status-card-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.status-card-icon {
  font-size: 0.85rem;
  color: var(--primary-cyan);
  width: 14px;
}

.status-card-title {
  font-size: 0.7rem;
  font-weight: 600;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.status-card-content {
  margin-top: 0.4rem;
}

.status-value {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.value-number {
  font-size: 1rem;
  font-weight: 600;
  color: var(--primary-cyan);
  font-family: 'Courier New', monospace;
}

.status-bar {
  width: 100%;
  height: 4px;
  background: var(--bg-tertiary);
  border-radius: 2px;
  overflow: hidden;
  position: relative;
  margin-top: 0.3rem;
}

.status-bar-fill {
  height: 100%;
  background: var(--primary-cyan);
  border-radius: 2px;
  transition: width var(--transition-slow);
  position: relative;
  box-shadow: 0 0 4px var(--primary-cyan);
}

.status-bar-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, hsla(0, 0%, 100%, 0.3), transparent);
  animation: shimmer 2s infinite;
}

.status-empty {
  padding: 2rem;
  text-align: center;
  color: var(--text-tertiary);
  font-size: 0.75rem;
  font-family: 'Courier New', monospace;
}

.status-detail {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  margin-top: 0.3rem;
  line-height: 1.4;
}

.risk-low {
  color: var(--accent-green);
}

.risk-medium {
  color: var(--accent-orange);
}

.risk-high {
  color: var(--accent-red);
}

.status-bar-fill.status-low {
  background: var(--accent-green);
}

.status-bar-fill.status-medium {
  background: var(--accent-orange);
}

.status-bar-fill.status-high {
  background: var(--accent-red);
}

.status-bar-fill.status-healthy {
  background: var(--accent-green);
}

.status-bar-fill.status-warning {
  background: var(--accent-orange);
}

.status-bar-fill.status-critical {
  background: var(--accent-red);
}

@keyframes shimmer {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}

.status-indicator-small {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
}

.status-dot-small {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--accent-teal);
  box-shadow: 0 0 8px var(--accent-teal);
}

.status-indicator-small.healthy .status-dot-small {
  background: var(--accent-teal);
  box-shadow: 0 0 8px var(--accent-teal);
}

/* ========== 状态详情 ========== */
.status-details {
  margin-top: 0.75rem;
}

.detail-section {
  padding: 0.75rem;
}

.detail-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.4rem;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.detail-title::before {
  content: '';
  width: 4px;
  height: 20px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.monitor-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 0.5rem;
}

.monitor-item {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  padding: 0.5rem 0.6rem;
  border-radius: var(--radius-xs);
  transition: all var(--transition-fast);
}

.monitor-item:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
}

.monitor-label {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-weight: 500;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
}

.monitor-value {
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--primary-cyan);
  font-family: 'Courier New', monospace;
}

/* ========== 占位符内容 ========== */
.placeholder-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
  min-height: 400px;
}

.placeholder-icon {
  font-size: 4rem;
  color: var(--primary-cyan);
  margin-bottom: 1.5rem;
  opacity: 0.6;
  animation: float 3s ease-in-out infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.placeholder-text {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.placeholder-subtext {
  font-size: 1rem;
  color: var(--text-secondary);
}

/* ========== 模态框 ========== */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: hsla(0, 0%, 0%, 0.7);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: none;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity var(--transition-normal);
}

.modal-overlay.active {
  display: flex;
  opacity: 1;
}

.modal-container {
  background: var(--gradient-glass);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid var(--border-color);
  border-radius: 16px;
  box-shadow: var(--shadow-lg);
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transform: scale(0.9);
  transition: transform var(--transition-normal);
}

.modal-overlay.active .modal-container {
  transform: scale(1);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-glass);
}

.modal-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.modal-close {
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.modal-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--text-primary);
  transform: rotate(90deg);
}

.modal-body {
  padding: 2rem;
  overflow-y: auto;
  flex: 1;
}

.modal-detail-content {
  color: var(--text-primary);
}

.modal-detail-content p {
  margin-bottom: 1rem;
  line-height: 1.6;
}

/* ========== 响应式设计 ========== */
@media (max-width: 1024px) {
  .main-content {
    flex-direction: column;
    padding: 0.5rem;
    gap: 0.5rem;
  }

  .header-content {
    flex-wrap: wrap;
    gap: 0.75rem;
  }

  .top-nav-menu {
    order: 3;
    width: 100%;
    justify-content: center;
    gap: 0.4rem;
    overflow-x: auto;
  }

  .nav-item {
    min-width: 100px;
    padding: 0.5rem 0.75rem;
    font-size: 0.85rem;
  }

  .nav-shortcut {
    width: 18px;
    height: 18px;
    font-size: 0.65rem;
  }

  .status-grid {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    gap: 0.75rem;
  }

  .entry-item {
    padding: 0.75rem;
    gap: 0.75rem;
  }

  .entry-icon {
    width: 32px;
    height: 32px;
    font-size: 1rem;
  }
}

@media (max-width: 768px) {
  .header-content {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .logo-text {
    font-size: 1rem;
  }

  .logo-icon {
    font-size: 1.5rem;
  }

  .status-grid {
    grid-template-columns: 1fr;
  }

  .monitor-grid {
    grid-template-columns: 1fr;
  }

  .entry-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }

  .entry-meta {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
  }
}

/* ========== 滚动条样式 ========== */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb {
  background: var(--primary-cyan);
  border-radius: 5px;
  border: 2px solid var(--bg-secondary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-teal);
}

/* ========== 选择文本样式 ========== */
::selection {
  background: var(--primary-cyan);
  color: var(--bg-primary);
}

::-moz-selection {
  background: var(--primary-cyan);
  color: var(--bg-primary);
}

/* ========== 淡入动画 ========== */
.fade-in {
  animation: fadeIn 0.6s ease-out forwards;
  opacity: 0;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========== 加载动画 ========== */
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.loading {
  display: inline-block;
  animation: spin 1s linear infinite;
}

/* ========== 条目式列表设计 ========== */
.entry-list {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.entry-item {
  display: flex;
  gap: 0.75rem;
  padding: 0.6rem 0.75rem;
  transition: all var(--transition-fast);
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
}

.entry-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--gradient-primary);
  transform: scaleY(0);
  transition: transform var(--transition-normal);
}

.entry-item:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-lg),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 26px hsla(187, 85%, 45%, 0.16);
}

.entry-item:hover::before {
  transform: scaleY(1);
}

.entry-icon {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: hsla(220, 18%, 12%, 0.85);
  border-radius: 8px;
  color: var(--primary-cyan);
  font-size: 0.75rem;
  border: 1px solid hsla(187, 85%, 45%, 0.18);
  transition: all var(--transition-fast);
}

.entry-item:hover .entry-icon {
  background: var(--primary-cyan);
  color: var(--bg-primary);
  border-color: var(--primary-cyan);
}

.entry-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  min-width: 0;
}

.entry-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.entry-title {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-primary);
  flex: 1;
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-family: 'Courier New', monospace;
}

.entry-status {
  padding: 0.15rem 0.4rem;
  border-radius: 3px;
  font-size: 0.65rem;
  font-weight: 600;
  text-transform: uppercase;
  flex-shrink: 0;
  font-family: 'Courier New', monospace;
}

.entry-status.completed {
  background: hsla(120, 60%, 50%, 0.2);
  color: hsl(120, 60%, 60%);
  border: 1px solid hsl(120, 60%, 50%);
}

.entry-status.in-progress {
  background: hsla(45, 80%, 50%, 0.2);
  color: hsl(45, 80%, 60%);
  border: 1px solid hsl(45, 80%, 50%);
}

.entry-status.pending {
  background: hsla(210, 60%, 50%, 0.2);
  color: hsl(210, 60%, 60%);
  border: 1px solid hsl(210, 60%, 50%);
}

.entry-status.unread {
  background: hsla(0, 70%, 50%, 0.2);
  color: hsl(0, 70%, 60%);
  border: 1px solid hsl(0, 70%, 50%);
}

.entry-status.read {
  background: hsla(210, 20%, 30%, 0.2);
  color: var(--text-tertiary);
  border: 1px solid var(--border-color);
}

.entry-meta {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  flex-wrap: wrap;
  font-size: 0.65rem;
  color: var(--text-tertiary);
  font-family: 'Courier New', monospace;
}

.entry-time,
.entry-date,
.entry-sender {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.entry-time::before,
.entry-date::before {
  content: '\f017';
  font-family: 'Font Awesome 6 Free';
  font-weight: 900;
  font-size: 0.75rem;
  opacity: 0.6;
}

.entry-sender::before {
  content: '\f007';
  font-family: 'Font Awesome 6 Free';
  font-weight: 900;
  font-size: 0.75rem;
  opacity: 0.6;
}

.entry-priority {
  padding: 0.15rem 0.35rem;
  border-radius: 3px;
  font-size: 0.65rem;
  font-weight: 600;
  flex-shrink: 0;
  font-family: 'Courier New', monospace;
}

.entry-priority.priority-urgent {
  background: hsla(0, 70%, 50%, 0.2);
  color: hsl(0, 70%, 60%);
  border: 1px solid hsl(0, 70%, 50%);
}

.entry-priority.priority-high {
  background: hsla(15, 70%, 50%, 0.2);
  color: hsl(15, 70%, 60%);
  border: 1px solid hsl(15, 70%, 50%);
}

.entry-priority.priority-medium {
  background: hsla(45, 70%, 50%, 0.2);
  color: hsl(45, 70%, 60%);
  border: 1px solid hsl(45, 70%, 50%);
}

.entry-priority.priority-low {
  background: hsla(210, 40%, 50%, 0.2);
  color: hsl(210, 40%, 60%);
  border: 1px solid hsl(210, 40%, 50%);
}

.entry-description {
  font-size: 0.7rem;
  color: var(--text-secondary);
  line-height: 1.4;
  margin-top: 0.2rem;
  word-wrap: break-word;
  white-space: normal;
}

.entry-item:hover .entry-description {
  color: var(--text-primary);
}

/* ========== 通讯模块对话界面 ========== */
.communication-body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  /* 覆盖 .module-body 的 padding，通讯模块有自己的内边距管理 */
  padding: 0 !important;
  /* 覆盖 .module-body 的 overflow-y: auto，通讯模块不需要整体滚动 */
  overflow: hidden !important;
}

.chat-messages {
  /* 使用 flex: 1 自动计算可用高度，不允许整体滚动 */
  flex: 1;
  min-height: 0;
  overflow: hidden;
  padding: 0.75rem 1rem;
  padding-bottom: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.9rem;
  /* 确保消息区域不滚动，所有内容在视口内可见 */
}

.message-item {
  display: flex;
  align-items: flex-end;
  gap: 0.35rem;
  padding: 0;
  /* flex 行为由子类（.message-user 和 .message-assistant）控制 */
  min-height: 0;
}

.message-item.message-user {
  justify-content: flex-end;
  /* user 消息根据内容自适应，不占据额外空间 */
  flex: 0 1 auto;
  min-height: 0;
}

.message-item.message-assistant {
  justify-content: flex-start;
  /* assistant 消息占据剩余的所有空间 */
  flex: 1;
  min-height: 0;
  /* 确保 assistant 消息能够充分利用可用空间 */
  align-items: stretch;
}

.message-item.message-assistant .message-bubble {
  /* assistant 消息的气泡应该填满父容器的高度 */
  height: 100%;
  width: 83%;
  max-width: 83%;
}

.message-bubble {
  position: relative;
  max-width: 96%;
  width: fit-content;
  /* 气泡本身不滚动，让内部的三栏各自滚动 */
  overflow: hidden;
  padding: 0.85rem 1rem;
  border-radius: 20px;
  background: var(
    --bubble-bg,
    radial-gradient(circle at 25% 25%, hsla(183, 70%, 35%, 0.18), hsla(183, 65%, 18%, 0.32))
  );
  border: 1px solid var(--bubble-border, hsla(183, 75%, 55%, 0.6));
  box-shadow:
    0 10px 26px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(4px);
  transition:
    transform var(--transition-fast),
    box-shadow var(--transition-fast),
    border-color var(--transition-fast);
  /* 使用 flex 布局让内部内容正确填充 */
  display: flex;
  flex-direction: column;
  /* 确保消息气泡能够充分利用可用空间 */
  min-height: 0;
}

.message-bubble:hover {
  transform: translateY(-1px);
  box-shadow:
    0 12px 30px rgba(0, 0, 0, 0.32),
    inset 0 1px 0 rgba(255, 255, 255, 0.12);
  border-color: hsla(183, 90%, 65%, 0.7);
}

.message-bubble::before,
.message-bubble::after {
  content: '';
  position: absolute;
  background: inherit;
  border: 1px solid inherit;
  border-radius: 50%;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.14);
}

.message-bubble::before {
  width: 14px;
  height: 14px;
  bottom: -7px;
}

.message-bubble::after {
  width: 9px;
  height: 9px;
  bottom: -14px;
  opacity: 0.85;
}

.message-assistant .message-bubble {
  --bubble-bg: radial-gradient(circle at 22% 20%, hsla(183, 80%, 42%, 0.24), hsla(183, 70%, 16%, 0.32));
  --bubble-border: hsla(183, 80%, 55%, 0.55);
}

.message-assistant .message-bubble::before {
  left: 16px;
}

.message-assistant .message-bubble::after {
  left: 6px;
}

.message-user .message-bubble {
  --bubble-bg: radial-gradient(circle at 78% 20%, hsla(280, 70%, 48%, 0.24), hsla(280, 50%, 16%, 0.32));
  --bubble-border: hsla(280, 70%, 60%, 0.55);
  align-self: flex-end;
}

.message-user .message-bubble::before {
  right: 16px;
}

.message-user .message-bubble::after {
  right: 6px;
}

/* ========== 通讯模块三栏消息样式 ========== */
.message-content-three-columns {
  display: flex;
  gap: 0.75rem;
  align-items: stretch;
  /* 允许内容区域填满气泡并启用滚动 */
  flex: 1;
  min-height: 0;
  overflow: visible;
  /* 不固定宽度，让容器根据内容动态增长 */
  width: auto;
  max-width: 100%;
  /* 允许三栏在空间不足时换行（如果需要） */
  flex-wrap: nowrap;
  /* 确保三栏始终同时显示 */
  justify-content: flex-start;
}

.message-column {
  /* 允许动态增长，但通过 max-width 限制每栏最大宽度为屏幕宽度的 25% */
  flex: 1 1 0;
  min-width: 0;
  max-width: 25vw; /* 每个栏最大宽度为屏幕总宽度的 25% */
  display: flex;
  flex-direction: column;
  /* 移除最小高度限制，让列能够充分利用可用空间 */
  min-height: 0;
  /* 每个列独立滚动 */
  overflow-y: auto;
  padding: 0.6rem;
}

.message-column-header {
  font-size: 0.85rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 0.4rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid var(--border-color);
}

.message-column-content {
  flex: 1;
  color: var(--text-primary);
  font-size: 0.85rem;
  line-height: 1.6;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', monospace;
}

.message-column::-webkit-scrollbar {
  width: 4px;
}

.message-column::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 2px;
}

.message-column::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 2px;
}

.message-column::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

.message-content {
  font-size: 0.85rem; /* 调大字体 */
  line-height: 1.8; /* 增加行间距 */
  color: var(--text-primary);
  word-wrap: break-word;
  white-space: pre-wrap;
  margin-top: 0.1rem;
  font-family: 'Courier New', monospace;
  width: 100%;
}

.message-time {
  font-size: 0.65rem;
  color: var(--text-tertiary);
  white-space: nowrap;
  margin-left: 0.4rem;
  font-family: 'Courier New', monospace;
  text-align: right;
  margin-top: 0.45rem;
}

.chat-input-container {
  flex-shrink: 0;
  padding: 0.75rem 1rem;
  padding-top: 0.5rem;
  border-top: 1px solid var(--border-color);
  background: var(--bg-glass);
  /* 固定高度，确保消息区域能正确计算剩余空间 */
  min-height: 60px;
  /* 确保输入框与消息区域有适当间距 */
  margin-top: auto;
}

.chat-input-wrapper {
  display: flex;
  align-items: flex-end;
  gap: 0.5rem;
  max-width: 100%;
}

.chat-input {
  flex: 1;
  min-height: 32px;
  max-height: 80px;
  padding: 0.4rem 0.6rem;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-primary);
  font-size: 0.75rem;
  font-family: 'Courier New', monospace;
  line-height: 1.4;
  resize: none;
  overflow-y: auto;
  transition: all var(--transition-fast);
}

.chat-input:focus {
  outline: none;
  box-shadow:
    0 0 0 2px hsla(187, 85%, 45%, 0.22),
    0 0 20px hsla(187, 85%, 45%, 0.12);
}

.chat-input::placeholder {
  color: var(--text-tertiary);
}

.chat-send-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-cyan);
  border: none;
  border-radius: 4px;
  color: var(--bg-primary);
  font-size: 0.8rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-send-btn:hover {
  background: var(--accent-teal);
  box-shadow: 0 0 6px var(--accent-teal);
}

.chat-send-btn:active {
  opacity: 0.8;
}

.chat-send-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* ========== Option 选项样式 ========== */
.chat-show-options-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-show-options-btn:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(187, 85%, 45%, 0.14);
  color: var(--primary-cyan);
}

.chat-show-options-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

/* ========== 重新生成按钮 ========== */
.chat-regenerate-btn {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background:
    var(--panel-bg) padding-box,
    var(--panel-border-gradient) border-box;
  border: 1px solid transparent;
  border-radius: var(--radius-xs);
  color: var(--text-secondary);
  font-size: 0.75rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-regenerate-btn:hover {
  background:
    var(--panel-bg-hover) padding-box,
    var(--panel-border-gradient) border-box;
  box-shadow:
    var(--shadow-md),
    0 0 0 1px var(--panel-border-strong) inset,
    0 0 18px hsla(30, 80%, 45%, 0.14);
  color: var(--accent-orange);
}

.chat-regenerate-btn:active {
  opacity: 0.8;
  transform: scale(0.95);
}

.chat-regenerate-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transform: none;
}

/* 选项浮层背景 */
.chat-options-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(15, 23, 42, 0.85);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  opacity: 0;
  visibility: hidden;
  transition:
    opacity var(--transition-normal),
    visibility var(--transition-normal);
  pointer-events: none;
}

.chat-options-overlay.active {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

/* 选项容器（浮层内容）- 现在只用于显示标题和关闭按钮 */
.chat-options-container {
  position: absolute;
  top: 1rem;
  right: 1rem;
  border-radius: var(--radius-md);
  padding: 0.75rem 1rem;
  box-shadow:
    0 8px 32px rgba(0, 0, 0, 0.5),
    0 0 16px hsla(180, 100%, 40%, 0.2);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  z-index: 1001;
  pointer-events: auto;
}

.chat-options-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 0.75rem;
}

.chat-options-title {
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--text-primary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.chat-options-close {
  width: 28px;
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  font-size: 0.8rem;
  cursor: pointer;
  transition: all var(--transition-fast);
}

.chat-options-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--primary-cyan);
}

/* 选项列表 - 全屏定位容器 */
.chat-options-list {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: visible;
}

.chat-options-list::-webkit-scrollbar {
  width: 6px;
}

.chat-options-list::-webkit-scrollbar-track {
  background: var(--bg-tertiary);
  border-radius: 3px;
}

.chat-options-list::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.chat-options-list::-webkit-scrollbar-thumb:hover {
  background: var(--primary-cyan);
}

/* ========== Chat Option 气泡（核心） ========== */

.chat-option {
  display: inline-flex;
  align-items: center;
  justify-content: center;

  opacity: 0;
  transform: translate(-50%, -50%) scale(0.85) rotate(var(--rot));
  transition:
    opacity 0.35s ease,
    transform 0.45s cubic-bezier(0.22, 1, 0.36, 1),
    filter 0.3s ease;

  animation: thought-float 4.5s ease-in-out infinite;
}

/* 念头浮现 */
.chat-option.show {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1) rotate(var(--rot));
}

/* 轻微漂浮 */
@keyframes thought-float {
  0% {
    transform: translate(-50%, -52%) rotate(var(--rot));
  }
  50% {
    transform: translate(-50%, -48%) rotate(var(--rot));
  }
  100% {
    transform: translate(-50%, -52%) rotate(var(--rot));
  }
}

/* 真正的"气泡" - 升级为更贴近 AI 的思维气泡风格 */
.chat-option-button {
  position: relative;

  width: 136px;
  height: 136px;
  aspect-ratio: 1 / 1;

  display: flex;
  align-items: center;
  justify-content: center;

  padding: 1.2rem; /* 增加内边距 */
  border-radius: 50% 45% 52% 48% / 48% 52% 45% 50%; /* 更不规则的圆，更有机感 */
  overflow: visible; /* 改为 visible 以允许显示子气泡 */

  /* 使用与 assistant 消息气泡一致的径向渐变 */
  background: radial-gradient(circle at 25% 25%, hsla(183, 80%, 42%, 0.28), hsla(183, 70%, 16%, 0.38));
  border: 1px solid hsla(183, 80%, 55%, 0.55);
  color: var(--text-primary);

  cursor: pointer;
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.4),
    inset 0 1px 1px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(8px); /* 增强磨砂玻璃效果 */
  will-change: transform, filter;
  transition:
    transform var(--transition-normal),
    box-shadow var(--transition-normal),
    border-color var(--transition-normal),
    background var(--transition-normal),
    filter var(--transition-normal);
}

/* Hover = 气泡扩张且发光 */
.chat-option-button:hover {
  transform: scale(1.12);
  background: radial-gradient(circle at 25% 25%, hsla(183, 90%, 50%, 0.35), hsla(183, 80%, 20%, 0.45));
  border-color: var(--primary-cyan);
  box-shadow:
    0 15px 35px rgba(0, 0, 0, 0.5),
    0 0 20px hsla(180, 100%, 40%, 0.4);
  filter: brightness(1.1);
}

/* 点击 = 气泡剧烈收缩 */
.chat-option-button:active {
  transform: scale(0.9);
  transition: transform 0.1s ease;
}

/* 文字样式 - 针对圆形气泡优化 */
.chat-option-text {
  font-size: 0.8rem; /* 稍微减小字体以适应圆形 */
  line-height: 1.3;
  text-align: center;
  width: 100%;
  max-height: 80%; /* 限制高度 */

  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3; /* 增加到3行 */
  line-clamp: 3;

  overflow: hidden;
  word-break: break-word;
  pointer-events: none;
  color: var(--text-primary);
  font-family: 'Courier New', monospace;
  font-weight: 500;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.8); /* 增强阴影，提升文字在磨砂玻璃上的辨识度 */
  padding: 0 0.8rem; /* 稍微增加内边距 */
}

/* hover：聚焦一个念头
   关键点：不要给 `.chat-option`（它在做 transform 动画）直接加 blur filter，
   否则在部分 WebView/Electron 下会出现“filter + transform 动画导致不绘制”的现象（看起来像消失）。
   这里把 blur 下放到 `.chat-option-button` 上，父容器只做 opacity。 */
.chat-options-list:has(.chat-option:hover) .chat-option:not(:hover) {
  opacity: 0.75; /* 只降低透明度，不直接 blur 容器 */
}
.chat-options-list:has(.chat-option:hover) .chat-option:not(:hover) .chat-option-button {
  filter: blur(3px) grayscale(0.2) brightness(0.95);
}

/* 兼容性回退：如果浏览器不支持 :has()，使用父容器 hover（依赖 list 可 hover） */
@supports not selector(:has(*)) {
  .chat-options-list:hover .chat-option:not(:hover) {
    opacity: 0.75;
  }
  .chat-options-list:hover .chat-option:not(:hover) .chat-option-button {
    filter: blur(3px) grayscale(0.2) brightness(0.95);
  }
}

.chat-option:hover {
  z-index: 10;
  opacity: 1 !important; /* 确保悬停项完全清晰 */
  filter: none !important;
}

.chat-option:hover .chat-option-button {
  filter: brightness(1.2) contrast(1.1); /* 进一步增强悬停项的视觉表现 */
  transform: scale(1.12);
  box-shadow:
    0 12px 32px rgba(0, 0, 0, 0.4),
    0 0 25px hsla(180, 100%, 40%, 0.5);
}

/* 被选中的念头：膨胀并高亮 */
.chat-option-button.selected {
  transform: scale(1.4);
  background: var(--primary-cyan);
  border-color: white;
  color: white;
  box-shadow: 0 0 40px hsla(180, 100%, 40%, 0.6);
  z-index: 100;
  animation: none;
}

.chat-option-button.selected .chat-option-text {
  color: white;
  text-shadow: 0 1px 3px rgba(0, 0, 0, 0.3);
}

/* 其他念头变模糊而不是完全消失 */
.chat-options-list.collapse .chat-option:not(.selected-parent) {
  opacity: 0.3; /* 保持微弱可见 */
  /* 同 hover 一样，避免给动画容器加 blur，改为 blur button */
  transform: translate(-50%, -50%) scale(0.85);
  pointer-events: none;
}
.chat-options-list.collapse .chat-option:not(.selected-parent) .chat-option-button {
  filter: blur(6px) grayscale(0.5) brightness(0.9);
}

.chat-option.selected-parent {
  z-index: 20;
}

/* 无选项提示消息 */
.chat-no-options-message {
  width: 100%;
  padding: 1rem;
}

.chat-no-options-content {
  border-radius: var(--radius-md);
  padding: 1rem 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.chat-no-options-text {
  color: var(--text-primary);
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
}

.chat-no-options-close {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 50%;
  color: var(--text-secondary);
  font-size: 1.2rem;
  cursor: pointer;
  transition: all var(--transition-fast);
  flex-shrink: 0;
}

.chat-no-options-close:hover {
  background: var(--bg-tertiary);
  border-color: var(--primary-cyan);
  color: var(--primary-cyan);
}

/* ========== 流式输出样式 ========== */
.message-streaming {
  opacity: 0.8;
}

.streaming-indicator {
  display: flex;
  align-items: center;
  gap: 0.4rem;
  margin-bottom: 0.3rem;
  color: var(--primary-cyan);
  font-size: 0.7rem;
}

.streaming-dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: var(--primary-cyan);
  animation: streaming-pulse 1.5s ease-in-out infinite;
}

@keyframes streaming-pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.5;
    transform: scale(0.8);
  }
}

.streaming-text {
  font-family: 'Courier New', monospace;
  font-size: 0.7rem;
}

.streaming-text-content {
  color: var(--text-primary);
  font-size: 0.75rem;
  line-height: 1.4;
  white-space: pre-wrap;
  word-wrap: break-word;
  font-family: 'Courier New', monospace;
  margin-top: 0.2rem;
}
